.itemIndividual {
    h1 {
        padding: 10px 0 10px 0;
    }
    h2 {
        padding: 10px 0 10px 0;
    }
    p {
        padding: 10px 0 10px 0;
    }
}

.volver{
    color: white;
    padding: 10px;
    max-width: 100px;
    background-color: rgba(0, 0, 0, 0.103);
    text-decoration: none;
    border-radius: 2px;
    &:visited {
        color: black;
    }
    &:link {
        color: black;
    }

}

.mas{
    color: black;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.103);
    text-decoration: none;
    border-radius: 2px;
    &:visited {
        color: black;
    }
    &:link {
        color: black;
    }
}

.contenedor {
    gap: 20px;
}


.comentario {
    font-size: 22px;
}