.itemCard {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    -webkit-box-shadow: 0px 0px 77px 5px rgba(153,153,153,0.12);
    -moz-box-shadow: 0px 0px 77px 5px rgba(153,153,153,0.12);
    box-shadow: 0px 0px 77px 5px #9999991f;
    border-radius: 10px;
    text-align: center;
    align-items: center;
    padding: 15px;

    &:hover {
        background-color: #9999991f;
        transition: background-color 0.5s;

    }
}

.itemCardImg {
    width: 300px;
    height: 300px;
}