nav {
    background-color: $colorPrimario;
    font-size: 18px;
    display: flex;
    align-items: center;

    .brand {
        font-size: 34px;
        background-color: white;
        font-style: italic;
    }

    div {
        .navbar-nav {
            align-items: center;
        }
        ul {
            

            li {
                margin: 3px 10px 0 10px;

                a {
                    color: white;
                }
            }
        }
    }
}

.carrito {
    width: 43px;
}

.bubble {
    width: 43px;
    font-size: 12px;
    border: 1px solid white;
    border-radius: 50px;
    color: black;
    background-color: white;
}

